import React, { useState } from 'react';
import { TitleWrapper, DivWrapper, InputWrapper } from './inputStyled';
import AttendanceService from '../../service/attendanceService';
import { succAudio, errorAudio } from '../../util/sound';
import Sound from 'react-sound';

let audio = new Audio(
  '/Users/boxianmo/Desktop/Code/Pa/attendance/src/util/sourndSource/error.mp3'
);

const InputData = () => {
  const [disabled, setDisabled] = useState(false);
  return (
    <div>
      <DivWrapper>
        <TitleWrapper>钢琴公寓签到</TitleWrapper>
        <InputWrapper
          autoFocus
          onKeyPress={(e) => handleChange(e, setDisabled)}
          disabled={disabled}
        />
      </DivWrapper>
    </div>
  );
};

function handleChange(e, setDisabled) {
  const inputValue = e.target.value;
  console.log('input', e.target.which);
  if (e.which === 13) {
    console.log('扫码机');
    console.log(inputValue);
    setDisabled(true);
    AttendanceService.attend(inputValue)
      .then((res) => {
        console.log('res', res);
        succAudio.play();
        window.location.reload();
      })
      .catch((err) => {
        console.error('err: ', err);
        audio.play();
        window.location.reload();
      })
      .finally(() => {
        window.location.reload();
      });
  }
}

export default InputData;
